.Contact {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgba(126, 165, 255, 0.4);
  padding: 50px 0;
  letter-spacing: 0.05em;
}

.Contact h2 {
  padding-bottom: 20px;
}

.Contact__Form {
  display: flex;
  flex-direction: column;
}

.Contact__Form input,
.Contact__Form textarea {
  margin-bottom: 10px;
}

.Contact__Form__Wrapper {
  width: 360px;
}
